import { useCallback, useReducer } from "react";
import Fuse from "fuse.js";

import {
  E_FuzzySearchActions,
  T_FuzzySearchAction,
  T_FuzzySearchState
} from "./types";
import { debounce } from "shared/helpers/optimization";
import { toNumber } from "shared/helpers/number";

export default function useFuzzySearch<T_SearchListItem>(
  fuzzySearch: Fuse<T_SearchListItem>,
  searchList: T_SearchListItem[]
) {
  const memoizedReducer = useCallback(createReducer(fuzzySearch), [
    fuzzySearch,
    searchList
  ]);
  const [state, dispatch] = useReducer(memoizedReducer, {
    inputValue: "",
    fuzzySearchResults: null
  });

  const updateFuzzySearchResults = useCallback(
    debounce(
      () => dispatch([E_FuzzySearchActions.setFuzzySearchResults]),
      200 // Update search results if 100ms passed after user has stopped typing
    ),
    []
  );

  const handleInputValueChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    dispatch([E_FuzzySearchActions.setInputValue, event.target.value]);
    updateFuzzySearchResults();
  };

  return {
    ...state,
    handleInputValueChange
  };
}

function createReducer<T_SearchListItem>(fuzzySearch: Fuse<T_SearchListItem>) {
  return function searchReducer(
    state: T_FuzzySearchState<T_SearchListItem>,
    [type, payload]: T_FuzzySearchAction
  ): T_FuzzySearchState<T_SearchListItem> {
    switch (type) {
      case E_FuzzySearchActions.setInputValue: {
        return {
          ...state,
          inputValue: payload
        };
      }
      case E_FuzzySearchActions.setFuzzySearchResults: {
        const inputValueStr = String(state.inputValue);
        const newSearchResults = fuzzySearch.search(inputValueStr);

        // Do not update state if new search results are empty
        if (
          inputValueStr.length > 2 &&
          toNumber(state.fuzzySearchResults?.length) > 1 &&
          newSearchResults.length === 0
        ) {
          return state;
        }

        return {
          ...state,
          fuzzySearchResults: newSearchResults
        };
      }
      default:
        return state;
    }
  };
}
